import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { OrderTypeListComponent } from './order-type-list/order-type-list.component';
import { AddOrderTypeComponent } from './add-order-type/add-order-type.component';
import { OrderTypeService } from 'src/app/service/order-type.service';


const routes: Routes = [
  {
    path: "",
    component: OrderTypeListComponent,
    data: { title: 'order_type_list' }
  },
  {
    path: "add",
    component: AddOrderTypeComponent,
    data: { title: 'add_order_type' }
  },
  {
    path: 'edit/:id',
    component: AddOrderTypeComponent,
    data: { title: 'edit_order_type' }
  },

]

@NgModule({
  declarations: [OrderTypeListComponent, AddOrderTypeComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    FormsModule,
    ReactiveFormsModule,
    FormValidationModule,
    PaginationModule,
    SharedModule,
  ], providers: [OrderTypeService]
})
export class OrderTypeModule { }

