
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonService } from 'src/app/service/common.service';
import { OrderTypeService } from 'src/app/service/order-type.service';
declare var document: any;

@Component({
  selector: 'app-add-order-type',
  templateUrl: './add-order-type.component.html'
})
export class AddOrderTypeComponent implements OnInit {

  PageTitle = "Order Type"

  loadingState = false;
  addForm: FormGroup;

  showLoader = false;
  formErrors = {
    order_type_code: null,
    apierror: null,
  };
  isEditing = false;
  editId = null;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private ordertypeService: OrderTypeService,
    private commonservice: CommonService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
    }

    this.addForm = this.fb.group({
      order_type_name: [null, Validators.compose([Validators.required])],
      order_type_code: [null, Validators.compose([Validators.required])],

    });
  }

  ngOnInit() {
    if (this.isEditing) {
      this.getEditObject()
    }
  }






  getEditObject() {
    this.ordertypeService.getOrderTypeById(this.editId).subscribe((response) => {
      if (response.success) {
        this.addForm.patchValue(response.data);
      } else {
        this.router.navigateByUrl('/manage/order-type');
      }
    });
  }

  submitForm(formData: any): void {
    if (formData.valid) {
      this.showLoader = true;
      const data = new FormData();
      data.append('order_type_name', formData.value.order_type_name);
      data.append('order_type_code', formData.value.order_type_code);

      if (this.isEditing) {
        this.ordertypeService.editOrderType(this.editId, data).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/manage/order-type');
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('order_type_code')) {
                this.formErrors['order_type_code'] = obj['order_type_code'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      } else {
        this.ordertypeService.addOrderType(data).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/manage/order-type');
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('order_type_code')) {
                this.formErrors['order_type_code'] = obj['order_type_code'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      }
    }
  }
}
